
[v-cloak] {
  display: none;
}































.bar-easy-vue {
  background-color: #4A90E2 !important;
}
.bar-easy-vue a, h1 {
  color: #fff !important;
}

.easy-card-bg {
  background-color:#fafafa !important;
}
.easy-card{
  margin: 20px 0px 50px 0px;
  border-top: 1px solid #eeefef;
  border-bottom: 1px solid #eeefef;
  border-radius: 0px;
  background-color: #fff;
}
.easy-card img{
  width: 100%;
  vertical-align: middle;
}
.easy-card > p{
  margin: 10px 10px;
}
.easy-card-loading{
  margin-bottom: 90px;
}
.easy-card-user-info{
  margin: 8px 10px;
  overflow: hidden;
}
.easy-card-user-info p{
  font-size: 14px;
  margin: 10px 0;
  color: #777;
}
.easy-card-user-info p:first-child{
  float: left;
}
.easy-card-user-info p:last-child{
  float: right;
  font-size: 14px;
  padding-top: 5px;
}
.easy-card-user-info p:first-child span{
  color: #1abc9c;
  font-size: 12px;
  padding-right: 2px;
}
.easy-card-user-info p:last-child span{
  color: #777;
  font-size: 14px;
  padding-right: 3px;
}
.easy-card-user-info .easy-favourited{
  color: #1abc9c !important;
}
.easy-card-user-info img{
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}

.nsr-loading-center {
  text-align: center;
  margin: 10px auto;
  color: #929292;
}
.nsr-loading-hiden {
  display: none;
}

.easy-bottom-bar {
  background-color: #4A90E2 !important;
  color: #fff;
  border: none;
}
.easy-bottom-bar a {
  color: #cdd !important;
}
.easy-fa-color {
  color: #fff !important;
}
